import React, { Component } from 'react'
import Cookie from 'js-cookie'
import Pubsub from "pubsub-js";

export default class settings extends Component {
  state = {
    image:"",
    username:"",
    bio:''
  }

  // 删除 Cookie 缓存
  logout = () => {
    Cookie.remove('auth')
    this.props.history.push('/')
    Pubsub.publish("remove",{auth:""})
    // 发布点击跳转事件
    Pubsub.publish("refnew1",{refNew:true})

  }

  // 渲染个人信息
  componentDidMount() {
    const {username,image,bio } = Cookie.getJSON("auth")
    this.setState({
      image,
      username,
      bio
    })
    return
  }

  //Change  输入框事件
  intChange = ()=>{
    return
  }




    render() {
        return (
            <div className="settings-page">
  <div className="container page">
    <div className="row">

      <div className="col-md-6 offset-md-3 col-xs-12">
        <h1 className="text-xs-center">Your Settings</h1>

        <form>
          <fieldset>
              <fieldset className="form-group">
                <input className="form-control" type="text" placeholder="URL of profile picture" value={this.state.image} onChange={this.intChange} />
              </fieldset>
              <fieldset className="form-group">
                <input className="form-control form-control-lg" type="text" placeholder="Your Name" value={this.state.username}  onChange={this.intChange} />
              </fieldset>
              <fieldset className="form-group">
                <textarea className="form-control form-control-lg" rows="8" placeholder="Short bio about you" value={this.state.bio}  onChange={this.intChange}></textarea>
              </fieldset>
              <fieldset className="form-group">
                <input className="form-control form-control-lg" type="text" placeholder="Email" />
              </fieldset>
              <fieldset className="form-group">
                <input className="form-control form-control-lg" type="password" placeholder="Password" />
              </fieldset>
              <button className="btn btn-lg btn-primary pull-xs-right">
                Update Settings
              </button>
          </fieldset>
        </form>
        <hr />
              <button className="btn btn-primary btn-outline-danger" onClick={this.logout}>
                Or click here to logout.
              </button>
      </div>

    </div>
  </div>
</div>
        )
    }
}


