import React, { Component } from 'react'
import axios from "axios"
import { BrowserRouter as Router,Route,Switch,Redirect} from "react-router-dom"
import Cookie from "js-cookie";
import Pubsub from "pubsub-js";

import "./App.css"

import Header from "./templates/layout/Header"
import Footer from "./templates/layout/Footer"
import Home from "./templates/pages/Home"
import Login from "./templates/pages/Login"
import Article from "./templates/pages/Article"
import Settings from "./templates/pages/Settings"
import Profile from "./templates/pages/Profile"

export default  class App extends Component {
  state = {
    refNew:true
  }
  
  componentDidMount(){
    // 测试
    axios({
      method:'get',
      url:"https://conduit.productionready.io/api/users/login"
    }).then((res)=>{
      // console.log(res);
    })
    axios({
      method:'get',
      url:"/api/profiles/鱼香Div"
    }).then((res)=>{
      // console.log(res);
    })

    // 帮助刷新页面
    //订阅
    Pubsub.subscribe("refnew1",(a,obj)=>{
      this.setState(
        obj
      )
    })
    Pubsub.subscribe("refnews",(b,obj)=>{
      this.setState(
        obj
      )
    })
  }
  
  
  
  
  
  

  render() {
    if(Cookie.getJSON("auth")){
      return (
        <div>
          <Router>
          <Header></Header>
            <Switch>
                <Route exact path="/" component={Home}/>
                <Route exact path="/login" component={Home} />
                <Route exact path="/register" component={ Home } />
                <Route exact path="/article" component={Article} />
                <Route exact path="/settings" component={Settings} />
                <Route exact path="/profile" component={Profile} />
                <Redirect to="/"/>
            </Switch>
          <Footer></Footer>
        </Router>
        </div>
      )
    }else{
      return (
        <div>
          <Router>
          <Header></Header>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route exact path="/login"  component={Login} />
                <Route exact path="/register" component={Login} /> 
                <Route exact path="/article" component={Home} />
                <Route exact path="/settings" component={Home} />
                <Route exact path="/profile" component={Home} />
                <Redirect to="/"/>
            </Switch>
          <Footer></Footer>
        </Router>
          
  
        </div>
      )
    }
    
  }
}
