import React, { Component } from "react";
import Pubsub from "pubsub-js";
import {request } from "../api/api";

import Cookie from "js-cookie";
// 注册用户名
class Yn extends Component {
  render() {
    return (
      <fieldset className="form-group">
        <input
          className="form-control form-control-lg"
          type="text"
          placeholder="Your Name"
        />
      </fieldset>
    );
  }
}
// 注册信息判断
class Em extends React.Component {
  render() {
    return (
      <ul className="error-messages">
        {/* <li>That email is already taken!</li> */}
      </ul>
    );
  }
}

export default class index extends Component {
  constructor() {
    super();
    this.state = {
      is: false,
      em: "",
      passw: "",
    };
    this.initEm = this.initEm.bind(this);
    this.initPass = this.initPass.bind(this);
    this.haddSubmit = this.haddSubmit.bind(this);
  }

  componentDidMount() {}
  islogin() {
    //判断是注册还是登陆  (register - Sign up)    ( login - Sign in)
    return this.props.match.path === "/register";
  }
  //  获取输入账号数据
  initEm(e) {
    this.setState({
      em: e.target.value,
    });
  }
  initPass(e) {
    this.setState({
      passw: e.target.value,
    });
  }
  // hadd  输入账号后登录
  haddSubmit(e) {
    const { em, passw } = this.state;
    //  取消默认跳转事件
    e.preventDefault();
    request({ 
      method: "post",
    url: "/api/users/login",
    data:{
        user: {
        email: em,
        password: passw,
      }
    }
  }).then((res) => {
        const data = res.data.user;
        const userAll = {
          token: data.token,
          username: data.username,
          image: data.image,
          updatedAt: data.updatedAt,
          bio:data.bio
        };
        if (res.status === 200) {
          // 发布
          Pubsub.publish("show", userAll);
          Cookie.set("auth", userAll);
          // 路由跳转
          this.props.history.push("/");
           // 帮助刷新页面, 发布点击跳转事件
          Pubsub.publish("refnews",{refNew:true})
        } else {
          return
        }
      });
  }

  
  
  render() {
    const { em, passw } = this.state;
    return (
      <div className="auth-page">
        <div className="container page">
          <div className="row">
            <div className="col-md-6 offset-md-3 col-xs-12">
              <h1 className="text-xs-center">
                {this.islogin() ? "Sign in" : "Sign up"}
              </h1>
              <p className="text-xs-center">
                <a href="##">
                  {this.islogin() ? "Have an account?" : "Need an account?"}
                </a>
              </p>

              {this.islogin() ? "" : <Em />}

              <form onSubmit={this.haddSubmit}>
                {this.islogin() ? "" : <Yn />}
                <fieldset className="form-group">
                  <input
                    onChange={this.initEm}
                    value={em}
                    className="form-control form-control-lg"
                    type="text"
                    placeholder="Email"
                  />
                </fieldset>
                <fieldset className="form-group">
                  <input
                    onChange={this.initPass}
                    value={passw}
                    className="form-control form-control-lg"
                    type="password"
                    placeholder="Password"
                  />
                </fieldset>
                <button className="btn btn-lg btn-primary pull-xs-right">
                  {this.islogin() ? "Sign in" : "Sign up"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
