
import React, { Component } from "react";
import {request } from "../api/api";
import { addFavorite, deleFavorite } from "../api/favorite";
import Cookie from "js-cookie";

export default class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //点赞用到的一条数据
      dataArr:{
        articles:[]
      },
      //点赞等待请求返回时，禁止连续点击
      forbidden:false,
      //筛选后的显示数据条数
      data: [],
      //页数
      page: [],
      // 当前页
      activepage:1
    };
  }

  componentDidMount() {
    // this.props.refresh()
    console.log(this.props);
    // 获取10条数据
    request({
      method: "get",
      url: "/api/articles?limit=10",
      params: {},
    }).then((res) => {
      let pages = [];
      //总页数 = 总条数（res.data.articlesCount ） / 每页显示的条数（res.data.articles.length）
      for (
        var i = 1;
        i <= res.data.articlesCount / res.data.articles.length;
        i++
      ) {
        // 循环生成全部页数
        pages.push(i);
      }
      this.setState({
        // 获取十条数据
        data: res.data.articles,
        // 循环生成的全部页数
        page: pages,
      });
    });

  }

  // 点击切换页数
  avalue = (e) => {
    request({
      method: "get",
      url: "/api/articles",
      params: {
        // 获取10条数据
        limit:10,
        // offset 从指定条数截取数据  当前页 (e.target.innerText - 1) * 每页长度（this.state.data.length）
        offset:(e.target.innerText - 1) * this.state.data.length
      },
    }).then((res) => {
      this.setState({
        // 获取十条数据
        data: res.data.articles,
        // 更新当前页
        activepage:e.target.innerText
      });
    });
  }
// 点赞
 like =async (data)=>{
  if(Cookie.getJSON("auth")){
    //  按钮禁用
  await this.setState({
    forbidden:true
  })
  if(data.favorited){
    await deleFavorite(data)
    data.favorited = false
    data.favoritesCount += -1
    let key = this.state.dataArr.articles.indexOf(data)
    this.state.dataArr.articles[key] = data
    await this.setState({
      articles:this.state.dataArr.articles
    })
  }else{
    await addFavorite(data).then((res)=>{
    })
    data.favorited = true
    data.favoritesCount += 1
    let key = this.state.dataArr.articles.indexOf(data)
    this.state.dataArr.articles[key] = data
    await this.setState({
      articles:this.state.dataArr.articles,
    })
  }
  //按钮开启
  await this.setState({
    forbidden:false
  })
  }else{
    await this.setState({
      forbidden:true
    })
    this.props.history.push("/register");
  }
}

  render() {
    return (
      <div className="home-page">
        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">
            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="##">
                      Your Feed
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="##">
                      Global Feed
                    </a>
                  </li>
                </ul>
              </div>

              {this.state.data.map((item, index) => {
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <a href="profile.html">
                        <img src={item.author.image} alt="" />
                      </a>
                      <div className="info">
                        <a href="##" className="author">
                          {item.author.username}
                        </a>
                        <span className="date">{item.createdAt}</span>
                      </div>
                      <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={()=>{this.like(item)}} disabled = {this.state.forbidden}>
                        <i className="ion-heart"></i> {item.favoritesCount}
                      </button>
                    </div>
                    <a href="##" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.description}</p>
                      <span>Read more...</span>
                    </a>
                  </div>
                );
              })}

              <list-pagination
                total-pages="$ctrl.listConfig.totalPages"
                current-page="$ctrl.listConfig.currentPage"
              >
                <nav>
                  <ul className="pagination">
                  {
                    this.state.page.map((item,index) => {
                      return (
                        <li
                      className={Number(this.state.activepage) === Number(item) ? "page-item active" :"page-item"}
                      key={index}
                    >
                      <a className="page-link" href="##" onClick={this.avalue}>
                        {item}
                      </a>
                    </li>
                      )
                    })
                  }
                  </ul>
                </nav>
              </list-pagination>
            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>

                <div className="tag-list">
                  <a href="##" className="tag-pill tag-default">
                    programming
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    javascript
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    emberjs
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    angularjs
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    react
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    mean
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    node
                  </a>
                  <a href="##" className="tag-pill tag-default">
                    rails
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
