import { request } from "./api";
import Cookie from "js-cookie";

// 点赞
export let addFavorite = (item) => {
//   console.log("=====点赞======");
  var slug = item.slug;
  request.interceptors.request.use(
    function (config) {
      // 携带token 保存到请求头
      if (Cookie.getJSON("auth")) {
        var  {token} =  Cookie.getJSON("auth")
      }
      config.headers.Authorization = "Token " + token;
      return config;
    },
    function (error) {
      // Do something with request error
      return Promise.reject(error);
    }
  );
  //发送点赞的请求
  return request({
    method: "post",
    url: `/api/articles/${slug}/favorite`
  })
};

// 取消点赞
export let deleFavorite = (item) => {
    // console.log("=====取消点赞======");
    var slug = item.slug;
    request.interceptors.request.use(
      function (config) {
        // 携带token 保存到请求头
        if (Cookie.getJSON("auth")) {
          var  {token} =  Cookie.getJSON("auth")
        }
        config.headers.Authorization = "Token " + token;
        return config;
      },
      function (error) {
        // Do something with request error
        return Promise.reject(error);
      }
    );
    //发送点赞的请求
    return request({
      method: "delete",
      url: `/api/articles/${slug}/favorite`,
      params: {},
    })
  };
  