import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Pubsub from "pubsub-js";
import Cookie from "js-cookie";

export default class header extends Component {
  constructor() {
    super();
    this.state = {
      token: "",
      image: "",
      username: "",
      auth:''
    };
  }
  componentDidMount() {
    Pubsub.subscribe("show", (m, stateObj) => {
      this.setState(stateObj);
    });
    if (Cookie.getJSON("auth")) {
      const { token,image, username } = Cookie.getJSON("auth");
      this.setState({
        token:token,
        image:image,
        username:username,
      });
    }
    Pubsub.subscribe("remove", (r, stateObj) => {
      this.setState(stateObj);
    });
  }

  render() {
    if (Cookie.getJSON("auth")) {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <NavLink className="navbar-brand" to="/">
                conduit
              </NavLink>

              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/" activeClassName="active" >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/article"
                    activeClassName="active"
                  >
                    <i className="ion-compose"></i>&nbsp;New Articles
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/settings"
                    activeClassName="active"
                  >
                    <i className="ion-gear-a"></i>&nbsp;Settings
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    activeClassName="active"
                    to="/profile"
                  >
                    <img src={this.state.image} className="user-pic" alt="" />
                    {this.state.username}
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    } else {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <NavLink className="navbar-brand" to="/">
                conduit
              </NavLink>

              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/" activeClassName="active" >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    activeClassName="active"
                    to="/register"
                    
                  >
                    Sign in
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    activeClassName="active"
                    to="/login"
                    
                  >
                    Sign up
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    }
  }
}
